.Row {
    display: flex;
    flex-direction: row;
    flex: 1 0 0;
}

.axisLabel {
    font-family: 'Roboto', sans-serif;
    font-weight: 600;
    /* transform: rotate(270deg); */
    position: absolute;
    width: fit-content;
    color: #ec2227;
}
.left {
    transform: rotate(270deg); 
}

.right{
    right: 0;
    transform: rotate(90deg);
}